






































import mInput from '../../components/m-input.vue'
export default {
	components: {
	    mInput
	},
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0,
			btntxt: '获取验证码',
			
			tel:"17607126635",
			code:"",
			npassword:"",
			repassword:"",
			token:'',
			
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			menuBorder: false,
			menuArrow: false,
			menuCard: false,
			disabled: false,
			isdisabled:true,
		};
	},
	onLoad() {
		const agentInfo = uni.getStorageSync('agentInfo');
		if (agentInfo) {
			this.token=agentInfo.token;
		}
	},
	methods: {
		navTo:function(){
			uni.redirectTo({
				url: '../tabbar/tabbar'
			});
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 80
		},
		changerDeposit(){
			if(this.npassword==''){
				uni.showToast({
					icon: 'none',
					title: '请输入新密码'
				});
				return;
			}else if(this.npassword.length<4){
				uni.showToast({
					icon: 'none',
					title: '密码最短为4位'
				});
				return;
			}else if(this.repassword==''){
				uni.showToast({
					icon: 'none',
					title: '请输入再次密码'
				});
				return;
			}
			uni.request({
				url: 'http://192.168.0.199:8080/agent/***/***',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST',
				dataType: 'json',
				cache: false,
				data: {
					token:this.token,
					npassword:this.npassword,
					repassword:this.repassword,
					code:this.code,
				},
				success: res => {
					
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
				complete: () => {}
				});
		},
		//验证手机号码部分
		sendcode() {
			var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
			//var url="/nptOfficialWebsite/apply/sendSms?mobile="+this.ruleForm.phone;
			if (this.tel == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入手机号码'
				});
			} else if (!reg.test(this.tel)) {
				uni.showToast({
					icon: 'none',
					title: '手机格式不正确'
				});
			} else {
				this.time = 60;
				this.disabled = true;
				this.timer();
				uni.showToast({
					icon: 'none',
					title: '验证码已发出，请注意查收'
				});
				uni.request({
					url: '',
					method: 'GET',
					data: {
						
					},
					success: res => {
						
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		timer() {
			if (this.time > 0) {
				this.time--;
				this.btntxt = this.time + 's后获取';
				setTimeout(this.timer, 1000);
			} else {
				this.time = 0;
				this.btntxt = '获取验证码';
				this.disabled = false;
			}
		}
	}
}
